import { Component, Input, OnInit } from '@angular/core';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsContainerHtmlViewer } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsComplexInappUrlComponent } from './ps-complex-inapp-url.component.interface';

@Component({
  selector: 'ps-complex-inapp-url',
  templateUrl: './ps-complex-inapp-url.component.html',
})
export class PsComplexInappUrlComponent extends PsBaseFieldComponent implements OnInit {


  @Input() options: IOptionsPsComplexInappUrlComponent;
  htmlViewerOptions: IOptionsPsContainerHtmlViewer = {};
  constructor(commonService: PsCommonService, logger: LoggerService) {
    super(commonService, logger);
  }

  ngOnInit() {
    // super.init();
    this.htmlViewerOptions.directURL = this.options.url;
    if (CommonUtils.isEmptyOrNull(this.options.type)) {
      this.options.type = 'iframe';
    }

    if (this.options.type == 'inapp' && this.options.url) {
      const browser = this.common.inAppBrowser.create(this.options.url);
      browser.show();
    }
  }

}
